<template>
  <div>
    <div style="min-height:170px">
      <div>常用</div>
      <div>
        <el-card
          v-for="item in markedSite"
          :key="item.name"
          shadow="hover"
          class="markedSite-container"
        >
          <a
            :href="item.url"
            target="_blank"
          >
            <img
              :src="item.iconUrl"
              class="markedSite-ico"
              onerror=''
            />
            <div
              :title="item.name"
              class="markedSite-name"
            >{{item.name}}</div>
          </a>
        </el-card>
      </div>
    </div>
    <div>
      <div>本地</div>
      <div>
        <el-card
          v-for="(item,index) in localMarkedSite"
          :key="index"
          shadow="hover"
          class="markedSite-container"
        >
          <div class="markedSite-options">
            <el-button
              circle
              class="el-icon-edit"
              @click="editMark(index)"
            ></el-button>
            <el-button
              circle
              class="el-icon-close"
              @click="deleteMark(index)"
            ></el-button>
          </div>
          <a
            :href="item.url"
            target="_blank"
          >
            <img
              :src="item.iconUrl"
              class="markedSite-ico"
              onerror=''
            />
            <div
              :title="item.name"
              class="markedSite-name"
            >{{item.name}}</div>
          </a>
        </el-card>
        <el-card
          shadow="hover"
          class="markedSite-container"
        >
          <div
            class="markedSite-ico el-icon-plus"
            style="font-size:50px;"
            @click="addSite()"
          ></div>
          <div
            class="markedSite-name"
            @click="addSite()"
          >添加</div>
        </el-card>
      </div>
    </div>
    <add-or-edit
      ref="addOrEdit"
      @savedHandle="savedHandle()"
    ></add-or-edit>
  </div>
</template>
<script>
import { queryMarkedSite } from './../assets/api/index'
import AddOrEdit from './AddOrEdit'
export default {
  data () {
    return {
      markedSite: [],
      localMarkedSite: []
    }
  },
  components: { AddOrEdit },
  created () { this.getMarkedSite() },
  methods: {
    getMarkedSite () {
      queryMarkedSite().then(data => {
        this.markedSite = data
        this.getlocalMarkedSite()
      })
    },
    getlocalMarkedSite () {
      let localMarkedSiteStr = localStorage.getItem('markedSite')
      if (localMarkedSiteStr) {
        this.localMarkedSite = JSON.parse(localMarkedSiteStr)
      }
    },
    addSite () {
      this.$refs.addOrEdit.showDialog()
    },
    deleteMark (index) {
      this.$confirm('确认删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.localMarkedSite.splice(index, 1)
        localStorage.setItem('markedSite', JSON.stringify(this.localMarkedSite))
        this.getlocalMarkedSite()
      })
    },
    editMark (index) {
      this.$refs.addOrEdit.openDialog(index)
    },
    savedHandle () {
      this.getlocalMarkedSite()
    }
  }
}
</script>
<style scoped>
.markedSite-container {
  width: 104px;
  height: 120px;
  margin: 10px;
  text-align: center;
  position: relative;
  overflow: visible;
  display: inline-block;
}
.markedSite-ico {
  width: 64px;
  height: 64px;
}
.markedSite-name {
  width: 64px;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.markedSite-options {
  position: absolute;
  top: -16px;
  right: -16px;
}
.el-button + .el-button {
  margin-left: 3px;
}
</style>



